@import url('https://kit.fontawesome.com/a076d05399.js');

.node {
    width: 25px;
    height: 25px;
    outline: 1px solid rgb(5, 15, 44);
    display: inline-block;
  }
  
  .node-finish {
    background-image: url('empire.png');
  }
  
  .node-start {
    background-image: url('spaceship.png');
  }
  
  .node-visited {
    animation-name: visitedAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      /* background-color: #181a22; */
      background-color: indigo;
      border-radius: 100%;
    }
  
    50% {
      background-color: midnightblue;
    }
  
    75% {
      transform: scale(1.2);
      /* background-color: #45a29e; */
      background-color: rgb(0,191,255);
    }
  
    100% {
      transform: scale(1);
      /* background-color: #66fcf1; */
      background-color: rgb(157,230,255);
    }
  }
  
  .node-wall {
    background-color: #3e7bfa;
  }
  
  .node-shortest-path {
    animation-name: shortestPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes shortestPath {
    0% {
      transform: scale(0.6);
      /* background-color: #c3073f; */
      background-color: peachpuff;
    }
  
    50% {
      transform: scale(1.2);
      /* background-color: #c3073f; */
      background-color: rgb(243, 140, 51);
    }
  
    100% {
      transform: scale(1);
      /* background-color: #c3073f; */
      background-color: rgb(243, 140, 51);
      /* background-image: url('start.png'); */
    }
  }