.node {
    width: 25px;
    height: 25px;
    outline: 1px solid rgb(5, 15, 44);
    display: inline-block;
  }
  
  .node-finish {
    background-image: url(data:image/png;base64,);
  }
  
  .node-start {
    background-image: url(data:image/png;base64,);
  }
  
  .node-visited {
    -webkit-animation-name: visitedAnimation;
            animation-name: visitedAnimation;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
  }
  
  @-webkit-keyframes visitedAnimation {
    0% {
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      /* background-color: #181a22; */
      background-color: indigo;
      border-radius: 100%;
    }
  
    50% {
      background-color: midnightblue;
    }
  
    75% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      /* background-color: #45a29e; */
      background-color: rgb(0,191,255);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      /* background-color: #66fcf1; */
      background-color: rgb(157,230,255);
    }
  }
  
  @keyframes visitedAnimation {
    0% {
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      /* background-color: #181a22; */
      background-color: indigo;
      border-radius: 100%;
    }
  
    50% {
      background-color: midnightblue;
    }
  
    75% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      /* background-color: #45a29e; */
      background-color: rgb(0,191,255);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      /* background-color: #66fcf1; */
      background-color: rgb(157,230,255);
    }
  }
  
  .node-wall {
    background-color: #3e7bfa;
  }
  
  .node-shortest-path {
    -webkit-animation-name: shortestPath;
            animation-name: shortestPath;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
  }
  
  @-webkit-keyframes shortestPath {
    0% {
      -webkit-transform: scale(0.6);
              transform: scale(0.6);
      /* background-color: #c3073f; */
      background-color: peachpuff;
    }
  
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      /* background-color: #c3073f; */
      background-color: rgb(243, 140, 51);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      /* background-color: #c3073f; */
      background-color: rgb(243, 140, 51);
      /* background-image: url('start.png'); */
    }
  }
  
  @keyframes shortestPath {
    0% {
      -webkit-transform: scale(0.6);
              transform: scale(0.6);
      /* background-color: #c3073f; */
      background-color: peachpuff;
    }
  
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      /* background-color: #c3073f; */
      background-color: rgb(243, 140, 51);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      /* background-color: #c3073f; */
      background-color: rgb(243, 140, 51);
      /* background-image: url('start.png'); */
    }
  }
